<style scoped>

.top-wrap,
.DatePicker-wrap {
    display: flex;
    justify-content: space-around;
}

.DatePicker-wrap {
    margin-top: 50px;
}

.top-wrap li {
    text-align: center;
    /*border: 1px solid #d7f0f2;*/
    padding: 10px;
    color: #c5c5c5;
}

.count {
    font: 22px '', Arial, '', Helvetica, sans-serif;
    color: #555;
}

.ivu-modal-content {
    width: 100%;
}

#main {
    width: 1200px !important;
    height: 400px;
}

.showRact,
.clickRact {
    display: inline-block;
    width: 30px;
    height: 15px;
}

.showRact {
    background: #044b7c;
}

.clickRact {
    background: #ea1107;
}

.ract-wrap {
    text-align: center;
    margin-bottom: 10px;
}

.ract-wrap div {
    display: inline-block;
}

.ract-wrap div span {
    display: inline-block;
    vertical-align: middle;
}

.ract-wrap div:nth-child(1) {
    margin-right: 100px;
}

.myDatePicker,
.myDatePicker_li {
    display: inline-block;
    color: black;
}

.myDatePicker {
    /*// margin-left: 500px;*/
}

.topTitle-wrap {
    display: flex;
    justify-content: space-between;
}

.fu .fu_ul,
.fu_code {
    display: flex;
    justify-content: space-between;
    background: #f7f7f7;
}

.fu_code_sx {
    font-size: 14px;
    display: inline-block;
    vertical-align: bottom;
    line-height: 28px;
    margin-right: 20px;
}

.orderName {
    display: inline-block;
    margin-left: 30px;
}

.orderId {
    display: inline-block;
    width: 50px;
}

</style>

<template>

<div>
    <Modal v-model="modal" :styles="{width:'100%'}" class-name="modal-box">
        <div class="topTitle-wrap">
            <div>
                <span style="color:#1c2438;font-size: 14px;"> ></span>
                <span style="color:#12bdce;"></span>
                <span>{{codeNameTitle}} {{codeGroupNameTitle}}</span>
            </div>
            <ul class="myDatePicker" slot="mySolt">
                <li class="myDatePicker_li">
                    <span></span>
                    <DatePicker @on-change="startTime" :value="dateRange" type="daterange" placeholder="" placement="bottom-end" :clearable=false :editable=false style="width: 200px;"></DatePicker>
                </li>
                <li class="myDatePicker_li">
                    <Button class="btnColor" style="margin-right:50px;" icon="archive" type="text" @click="confrimDownLoad">
                        
                    </Button>
                </li>
            </ul>
        </div>

        <RadioGroup v-model="selectMode" type="button" size="large" style="margin: 20px" @on-change="changeReport">
            <Radio :label="1"></Radio>
            <Radio :label="2"></Radio>
        </RadioGroup>
        <div id="main" style=""></div>
        <div class="ract-wrap">
            <div>
                <span class="showRact"></span>
                <span></span>

            </div>
            <div class="ract-wrap">
                <span class="clickRact"></span>
                <span></span>

            </div>
        </div>
        <div class='fu' v-show="isShowCode">
            <Table :columns="columns" :data="data"></Table>

            <Page :total="codeTotalCount" :current="pageNo" style="display: flex; justify-content: flex-end;margin-top: 5px;margin-bottom: 5px" show-sizer show-total @on-change="changePageNoCodeGroup" @on-page-size-change="changePageSize"></Page>

        </div>
        <div class='fu' v-show="isShowCodeGroup">
            <Table :columns="codeGroupColumns" :loading="loadingGroup" :data="codeGroupData"></Table>
            <Page :total="codeGroupTotalCount" :current="pageNo" style="display: flex; justify-content: flex-end;margin-top: 5px;margin-bottom: 5px" show-sizer show-total @on-change="changePageNoCodeGroup" @on-page-size-change="changePageSize"></Page>
        </div>
        <div slot="footer">
            <Button class="btnColor " type="text" @click="modal=false"></Button>

        </div>
    </Modal>
</div>

</template>

<script>

import topTitle from '@/components/common/topTitle'
import echarts from 'echarts'

export default {
    components: {
        topTitle
    },
    data() {
        return {
            loadingGroup: true,
            codeArr: [],
            codeNameTitle: '',
            codeGroupNameTitle: '',
            dataClickNum: [],
            dataShowNum: [],
            dataAdDateDay: [],
            changeCode: '',
            modal: false,
            value1: '',
            myChart: '',
            day7Count: '',
            day30Count: '',
            yesterdayCount: '',
            startDay: '',
            endDay: '',
            dateRange: [],
            pageSize: 10,
            pageNo: 1,
            codeId: '',
            codeGroupId: '',
            codeGroup: [],
            nameLike: '',
            selectMode: 1,
            codeByCodeGroupList: [],
            data: [],
            codeGroupData: [],
            showNum: '',
            clickNum: '',
            clickRate: '',
            isShowCode: true,
            isShowCodeGroup: false,
            codeTotalCount:0,
            codeGroupTotalCount: 0,
            dateDown: 1,
            columns: [{
                title: '',
                key: 'codeName'
            }, {
                title: 'Id',
                key: 'codeId'
            }, {
                title: '',
                key: 'showNum'
            }, {
                title: '',
                key: 'cliclNum'
            }, {
                title: '',
                key: 'cliclOdds'
            }, ],
            codeGroupColumns: [{
                title: '',
                key: 'codeGroupName'
            }, {
                title: '',
                key: 'codeGroupType'
            }, {
                title: '',
                key: 'showNum'
            }, {
                title: '',
                key: 'cliclNum'
            }, {
                title: '',
                key: 'cliclOdds'
            }, ]
        }
    },
    created() {
        // this.getBasicReport();
        // this.getCodeGroup();
    },
    mounted() {
        this.startDay = this.getPreDate(-30); //
        this.endDay = this.getPreDate(-1);
        this.dateRange = [this.startDay, this.endDay];
        this.myChart = echarts.init(document.getElementById('main'));
        // this.getOverallReportList();
        // this.getCodeReportChart(this.codeId);
    },
    methods: {
        beforeLoad() {
                this.modal = true;
                this.selectMode = 1;
                this.startDay = this.getPreDate(-30); //
                this.endDay = this.getPreDate(-1);
                this.dateRange = [this.startDay, this.endDay];
            },
            initTitle(codeName, codeGroupId) {
                this.codeNameTitle = codeName;
                this.$Http.get('codeGroup/getCodeGroupById.json?codeGroupId=' + codeGroupId, {}).then(res => {
                    if (res.body.status === 0) {
                        this.codeGroupNameTitle = res.body.data.codeGroupName
                    }
                })
            },
            initEcharts() {

                var option = {
                    title: {
                        text: ''
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.dataAdDateDay,
                    },
                    // yAxis: {
                    //   type: 'value'
                    // },
                    // series: [
                    //   {
                    //     name: '',
                    //     data: this.dataShowNum,
                    //     type: 'line',
                    //     areaStyle: {
                    //       color: '#d6eaff',
                    //     },
                    //
                    //
                    //   },
                    //   {
                    //     name: '',
                    //     data: this.dataClickNum,
                    //     type: 'line',
                    //     areaStyle: {
                    //       color: '#ffd6e1',
                    //     },
                    //
                    //
                    //   }
                    // ]


                    yAxis: [
                        // type: 'value'
                        {
                            name: '',
                            type: 'value'
                        }, {
                            name: '',
                            type: 'value',

                        }
                    ],
                    minInterval: 1,
                    series: [{
                        name: '',
                        data: this.dataShowNum,
                        type: 'line',
                        areaStyle: {
                            color: '#d6eaff',
                        },
                        lineStyle: {
                            color: '#044b7c',
                        },

                    }, {
                        name: '',
                        data: this.dataClickNum,
                        yAxisIndex: 1,
                        type: 'line',
                        areaStyle: {
                            color: '#ffd6e1',
                        },
                        lineStyle: {
                            color: '#ea1107',
                        },


                    }]


                };

                // 
                this.myChart.setOption(option);
            },
            getPreDate(days) {
                let date = new Date(); //
                date.setDate(date.getDate() + days); // -1 
                return date.Format("yyyy-MM-dd");
            },

            getCodeReportList(codeId, codeGroupId) {
                this.codeGroupId = codeGroupId;
                this.codeId = codeId;
                const url = `/report/getCodeReportByCodeId.json?startDay=${this.startDay}&endDay=${this.endDay}&codeId=${codeId}&pageSize=${this.pageSize}&pageNo=${this.pageNo}`;
                let tabList = "";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        console.log(res);

                        this.data = [];
                        let arr = res.body.data.result;
                        this.codeTotalCount = res.body.data.totalCount;
                        for (let i = 0; i < arr.length; i++) {
                            tabList = {
                                'codeName': arr[i].showName,
                                'codeId': arr[i].showId,
                                'showNum': arr[i].showNum,
                                'cliclNum': arr[i].clickNum,
                                'cliclOdds': arr[i].rate
                            }
                            this.data.push(tabList);
                        }
                    }

                })
            },
            getCodeGroupReportList(codeId, codeGroupId) {
                this.codeGroupId = codeGroupId;
                this.codeId = codeId;
                const url = `/report/getCodeGroupReportListByGroupId.json?startDay=${this.startDay}&endDay=${this.endDay}&codeGroupId=${codeGroupId}&pageSize=${this.pageSize}&pageNo=${this.pageNo}`;
                let tabList = "";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        this.loadingGroup = false;
                        console.log(res)
                        this.codeGroupData = [];
                        let arr = res.body.data.result;
                        this.codeGroupTotalCount = res.body.data.totalCount;
                        for (let i = 0; i < arr.length; i++) {
                            tabList = {
                                'codeGroupName': arr[i].showName,
                                'codeGroupType': arr[i].codeGroupType == 1 ? "" : "",
                                'showNum': arr[i].showNum,
                                'cliclNum': arr[i].clickNum,
                                'cliclOdds': arr[i].rate
                            }
                            this.codeGroupData.push(tabList);
                        }
                    }

                })
            },
            getCodeReportChart(codeId) {
                const url = `/report/getCodeReportChartByCodeId.json?startDay=${this.startDay}&endDay=${this.endDay}&codeId=${codeId}`;
                let tabList = "";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status === 0) {
                        console.log(res)
                        this.dataAdDateDay = [];
                        this.dataClickNum = [];
                        this.dataShowNum = [];
                        let chartList = res.body.data.result;
                        let clickNumArr = [];
                        let showNumArr = [];
                        chartList.forEach((item, index) => {
                            clickNumArr.push(item.clickNum);
                            showNumArr.push(item.showNum);
                            this.dataAdDateDay.push(item.adDateDay);

                        })
                        this.dataClickNum = clickNumArr.reverse();
                        this.dataShowNum = showNumArr.reverse()
                        this.dataAdDateDay.reverse()
                        this.myChart.clear();
                        this.initEcharts();

                    }


                })
            },
            getCodeGroupReportChart() {
                const url = `/report/getCodeGroupReportChartByGroupId.json?startDay=${this.startDay}&endDay=${this.endDay}&codeGroupId=${this.codeGroupId}`;
                this.$Http.get(url, {}).then(res => {
                    console.log(res)
                    if (res.body.status === 0) {
                        this.dataAdDateDay = [];
                        this.dataClickNum = [];
                        this.dataShowNum = [];
                        let chartList = res.body.data.result;
                        let clickNumArr = [];
                        let showNumArr = [];
                        chartList.forEach((item, index) => {
                            clickNumArr.push(item.clickNum);
                            showNumArr.push(item.showNum);
                            this.dataAdDateDay.push(item.adDateDay);

                        })
                        this.dataAdDateDay.reverse()
                        this.dataClickNum = clickNumArr.reverse();
                        this.dataShowNum = showNumArr.reverse()
                        this.dataAdDateDay.reverse()
                        this.myChart.clear();
                        this.initEcharts();

                    }


                })
            },
            changeReport(data) {
                console.log(data)
                this.dateDown = data
                if (data === 1) {
                    this.dateDown = data
                    this.isShowCode = true;
                    this.isShowCodeGroup = false;
                    this.getCodeReportChart(this.codeId)
                    this.getCodeReportList(this.codeId, this.codeGroupId);
                } else if (data === 2) {
                    this.isShowCode = false;
                    this.isShowCodeGroup = true;
                    this.getCodeGroupReportChart();
                    this.getCodeGroupReportList(this.codeId, this.codeGroupId);
                }
            },
            startTime(data) {
                if (data.length < 2 || data[0] == '' || data[1] == '') {
                    this.$Message.error('');
                    return false;
                }
                this.startDay = data[0];
                this.endDay = data[1];
                this.pageNo = 1;
                this.getCodeReportChart(this.codeId);
                this.getCodeReportList(this.codeId, this.codeGroupId);

            },
            changePageNoCodeGroup(pageNo) {
                this.loadingGroup = true;
                //this.pageSize = 10;
                this.pageNo = pageNo;
                //this.getCodeGroupReportList(this.codeId, this.codeGroupId);
                this.selectModeList();
            },
            changePageSize(pageSize) {
                this.loadingGroup = true;
                this.pageSize = pageSize;
                this.pageNo = 1;
                this.selectModeList();
            },

            selectModeList() {
                if (this.dateDown == 1) {
                    this.pageNo = 1;
                    this.getCodeReportList(this.codeId, this.codeGroupId);
                } else {
                    this.pageNo = 1;
                    this.getCodeGroupReportList(this.codeId, this.codeGroupId);
                }
            },

            confrimDownLoad() {

                const token = sessionStorage.getItem('token');

                if (this.dateDown == 1) {
                    window.location.href = this.ApiUrl + `/report/downloadCodeReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&codeId=${this.codeId}&t=${token}`;
                } else {
                    window.location.href = this.ApiUrl + `/report/downloadCodeGroupReportListByGroupId.json?startDay=${this.startDay}&endDay=${this.endDay}&codeGroupId=${this.codeGroupId}&t=${token}`;
                }

            },
    }
}

</script>



