<style scoped>

.table {
    margin-top: 10px;
}

.child-wrap {
    padding: 10px;
}

.select-wrap {
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    margin-top: 10px;
}

.select-wrap {
    /*display: inline-block;*/
}

.child-li {
    display: flex;
    /*justify-content: space-around;*/
}

.label-left {
    display: inline-block;
    width: 80px;
}

.child-div {
    margin-left: 20px;
    margin-top: 20px;
}

.ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
}

.seNewCodeModal {
    width: 500px;
}

.ivu-modal-wrap {
    left: none;
}

.topBtnWrap {
    display: flex;
    justify-content: space-between;
}

</style>

<template>

<div>
    <top-title title=""></top-title>
    <div class="child-wrap">
        <div class="form-full">
            <div>
                <span></span>
                <!-- <Cascader :data="codeGroup" @on-change="changeOption" style="width:200px;display:inline-block;" change-on-select filterable>
                </Cascader> -->
                <div class="main-element">
                    <el-cascader style="width:200px" :options="codeGroup" @change="changeOption" v-model="selectedOptions" change-on-select filterable clearable></el-cascader>
                </div>
            </div>
        </div>
        <div>
            <Tabs :animated="false">
                <TabPane label="">
                    <div class="topBtnWrap">
                        <div class="topBtn">
                            <Button class="btnColor" @click="getTemplateCodeMang" icon="plus" type="primary"></Button>
                            <Button class="btnSuspend" type="primary" :disabled="isStop" @click="stopAll"></Button>
                            <Button class="btnCancel" @click='deleteAll' type="error" :disabled="isDeletes"></Button>
                            <Button class="btnStart" type="success" @click="addAll" :disabled="isAdds"></Button>
                            <Button type="ghost" :icon="sxIcon" @click="sx"></Button>
                        </div>
                        <div class="topBtn">
                            <Input v-model="nameLike" placeholder="" style="width: 300px"></Input>
                            <Button class="btnColor" @click='search' type="primary" icon="search"></Button>
                        </div>
                    </div>

                    <div v-show="showBol2">
                        <ul class="select-wrap">
                            <li class="child-li">
                                <div class="child-div">
                                    <span class="label-left"></span>
                                    <Select @on-change="sxData" v-model="isNative" style="width:200px">
                                        <Option value="-99"></Option>
                                        <Option value="1"></Option>
                                        <Option value="0"></Option>
                                    </Select>
                                </div>
                                <div class="child-div">
                                    <span class="label-left"></span>
                                    <Select @on-change="sxData" v-model="status" style="width:200px">
                                        <Option value="-99"></Option>
                                        <Option value="1"></Option>
                                        <Option value="2"></Option>
                                        <Option value="0"></Option>
                                    </Select>
                                </div>
                                <div class="child-div">
                                    <span class="label-left"></span>
                                    <Select @on-change="sxData" v-model="useStatus" style="width:200px">
                                        <Option value="-99"></Option>
                                        <Option value="1"></Option>
                                        <Option value="2"></Option>
                                        <Option value="3"></Option>
                                    </Select>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <Table class="table" :loading="loading" @on-selection-change="selectData" border ref="selection" :columns="columns" :data="data1"></Table>
                    <Page :current="pageNo" @on-change='clickPage' :total="totalCount" show-sizer show-total @on-page-size-change="changePageSizeCode"></Page>
                </TabPane>
                <TabPane label="">
                    <div class="topBtnWrap">
                        <div class="topBtn">
                            <Button class="btnColor" @click="getTemplateCodeGroup" icon="plus" type="primary"></Button>
                            <Button class="btnCancel" @click='deleteGroupAll' type="error" :disabled="isGroupDalete"></Button>
                        </div>
                        <div>
                            <Input v-model="nameLikeGroup" placeholder="" style="width: 300px"></Input>
                            <Button class="btnColor" @click='searchGroup' type="primary" icon="search"></Button>
                        </div>
                    </div>

                    <Table class="table" :loading="loadingGroup" @on-selection-change="selectGroupData" border ref="selection" :columns="columnsGroup" :data="dataGroup"></Table>
                    <Page :current="pageNoGroup" @on-change='clickPageGroup' :total="totalCountGroup" show-sizer show-total @on-page-size-change="changePageSizeGroup"></Page>
                </TabPane>
            </Tabs>
        </div>

    </div>
    <setNew-codeMang ref="setNewCodeMang" v-on:filterParent="getCodeList"></setNew-codeMang>
    <setNew-codeGroup ref="setNewCodeGroup" v-on:filterParent="filterParentCodeGroup"></setNew-codeGroup>
    <updata-Code ref="updataCode" v-on:filterParent="getCodeList"></updata-Code>
    <see-code ref="seeCode"></see-code>
    <useAd-States ref="useAdStates" :adUses="adUseStates" v-on:filterParent="getCodeList"></useAd-States>
    <updata-CodeGroup ref="updataCodeGroup" v-on:filterParent="filterParentCodeGroup"></updata-CodeGroup>
    <report ref="report"></report>
</div>

</template>

<script>

// import PageSize from '../../common/PageSize'
import topTitle from '@/components/common/topTitle'
import setNewCodeMang from './child/setNewCodeMang'
import setNewCodeGroup from './child/setNewCodeGroup'
import updataCode from './child/updataCode'
import seeCode from './child/seeCode'
import useAdStates from './child/useAdStates'
import updataCodeGroup from './child/updataCodeGroup'
import report from './child/report'

export default {
    components: {
        topTitle, setNewCodeMang, setNewCodeGroup, updataCode, useAdStates, updataCodeGroup, report, seeCode
    },
    data() {
        return {
            tenplate: null,
            selectedOptions: [],
            a: true,
            value: '',
            loading: true,
            loadingGroup: true,
            modal1: false,
            showBol2: false,
            disabled: true,
            sxIcon: 'chevron-down',
            nameLike: '',
            nameLikeGroup: '',
            status: '',
            isNative: '',
            useStatus: '',
            pageSize: 10,
            pageSizeGroup: 10,
            totalCount: 0,
            totalCountGroup: 0,
            pageNo: 1,
            pageNoGroup: 1,
            codeSize: '',
            codeGroup: [],
            modelGroup: "",
            data1: [],
            dataGroup: [],
            isAdds: true,
            isDeletes: true,
            isStop: true,
            isGroupDalete: true,
            isGroupDaleteParams: [],
            addCodeParams: [],
            deleteCodeParams: [],
            useCodeParams: [],
            codeGroupId: '',
            adUseStates: '',
            columns: [{
                type: 'selection',
                width: 60,
                align: 'center'
            }, {
                title: '',
                key: 'codeName'
            }, {
                title: 'Id',
                key: 'fxAdCodeId',
                width: 100,
            }, {
                title: '',
                key: 'codeStatusstr',
                width: 100,
            }, {
                title: '',
                key: 'type',
                width: 150,
            }, {
                title: '',
                key: 'update'
            }, {
                title: '',
                key: 'use',
                render: (h, params) => {
                    if (params.row.use > 0) {
                        return h('div', [
                            h('a', {
                                style: {
                                    "text-decoration": 'underline'
                                },

                                on: {
                                    click: () => {
                                        this.adUseStates = {
                                            'useNums': params.row.use,
                                            'deliverIds': params.row.deliverIds

                                        }
                                        this.$refs.useAdStates.beforeLoad();
                                        this.$refs.useAdStates.getDeliverById(params.row.deliverIds);
                                    }
                                }
                            }, params.row.use + "")
                        ])
                    }
                    return h('div', [
                        h('p', {
                            style: {},
                            on: {
                                click: () => {
                                    // alert(params.row.use)
                                }
                            }
                        }, params.row.use + "")
                    ])


                }
            }, {
                title: '',
                key: 'operation',
                width: 300,
                align: 'center',
                render: (h, params) => {
                    var reportBtn = h('Button', {
                        props: {
                            type: 'primary',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px',
                            backgroundColor: '#0676c4',
                            borderColor: '#0676c4'
                        },
                        on: {
                            click: () => {
                                this.$refs.report.beforeLoad();
                                this.$refs.report.initTitle(params.row.codeName, params.row.fxAdCodeGroupId);
                                this.$refs.report.getCodeReportList(params.row.fxAdCodeId, params.row.fxAdCodeGroupId);
                                this.$refs.report.getCodeReportChart(params.row.fxAdCodeId);
                                this.$refs.report.getCodeGroupReportList(params.row.fxAdCodeId, params.row.fxAdCodeGroupId)
                            }
                        }
                    }, '');

                    var codeBtn = h('Button', {
                        props: {
                            type: 'info',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px',
                            backgroundColor: '#19be6b',
                            borderColor: '#19be6b'
                        },
                        on: {
                            click: () => {


                                this.$refs.seeCode.beforeLoad(params.row.fxAdCodeId);

                            }
                        }
                    }, '');

                    var editBtn = h('Button', {
                        props: {
                            type: 'info',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px',
                            backgroundColor: '#1fa2fd',
                            borderColor: '#1fa2fd'
                        },
                        on: {
                            click: () => {
                                this.$refs.updataCode.beforeLoad(params.row.codeStatus);
                                this.$refs.updataCode.getCodeById(params.row.fxAdCodeId);
                                this.$refs.updataCode.getCodeGroup();
                            }
                        }
                    }, '');

                    var deleteBtn = h('Button', {
                        props: {
                            type: 'error',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px',
                            backgroundColor: '#ea0b00',
                            borderColor: '#ea0b00'
                        },
                        on: {
                            click: () => {
                                let v = [];
                                v.push(params.row);
                                this.deleteBox(v);
                            }
                        }
                    }, '');

                    if (params.row.codeStatus === 0) {
                        return h('div', [reportBtn])
                    } else {
                        if (params.row.isNative === 0) {

                            return h('div', [codeBtn, editBtn, reportBtn, deleteBtn])

                        } else {

                            return h('div', [editBtn, reportBtn, deleteBtn])
                        }

                    }


                }
            }],
            columnsGroup: [{
                type: 'selection',
                width: 60,
                align: 'center'
            }, {
                title: '',
                key: 'codeName'
            }, {
                title: '',
                key: 'type'
            }, {
                title: '',
                key: 'update'
            }, {
                title: '',
                key: 'type2'
            }, {
                title: '',
                key: 'operation',
                width: 200,
                align: 'center',
                render: (h, params) => {
                    return h('div', [
                        h('Button', {
                            props: {
                                type: 'info',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#1fa2fd',
                                borderColor: '#1fa2fd'
                            },
                            on: {
                                click: () => {
                                    this.$refs.updataCodeGroup.beforeLoad();
                                    this.$refs.updataCodeGroup.getCodeGroupById(params.row.fxAdCodeGroupId);
                                }
                            }
                        }, ''),
                        h('Button', {
                            props: {
                                type: 'error',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px',
                                backgroundColor: '#ea0b00',
                                borderColor: '#ea0b00'
                            },
                            on: {
                                click: () => {
                                    let v = [];
                                    v.push(params.row);
                                    this.deleteGroupBox(v);
                                }
                            }
                        }, '')
                    ])
                }
            }]
        }
    },
    created() {

        if (this.$route.params.flag == 1) {
            this.useStatus = '3';
            this.sx();
        }
        this.getCodeGroup();
        this.getCodeList();
        this.codeGroupList();
        //this.getCodeGroupStageList();
    },
    methods: {
        //1 2 0
        selectData(selection) {
                console.log(selection)
                let canDeletes = selection.filter((item) => {
                    return item.codeStatus != 0;
                });

                let canAdds = selection.filter((item) => {
                    return item.codeStatus == 2;
                });
                let canStop = selection.filter((item) => {
                    return item.codeStatus == 1;
                })
                this.isAdds = !canAdds.length > 0;
                this.addCodeParams = canAdds;

                this.isDeletes = !canDeletes.length > 0;
                this.deleteCodeParams = canDeletes;

                this.isStop = !canStop.length > 0;
                this.useCodeParams = canStop;
                console.log(this.useCodeParams)
            },
            selectGroupData(selection) {

                let canDelate = selection.filter((item) => {
                    return item.codeGroupStatus;
                });
                this.isGroupDaleteParams = canDelate;
                this.isGroupDalete = !canDelate.length > 0;
                console.log(canDelate)
            },
            deleteGroupAll() {
                this.deleteGroupBox(this.isGroupDaleteParams);

            },

            stopAll() {
                this.stopBox(this.useCodeParams);
            },
            deleteAll() {
                this.deleteBox(this.deleteCodeParams);

            },
            addAll() {
                this.addBox(this.addCodeParams);

            },
            addBox(params) {

                let codeNames = params.map((item, index) => {
                    return item.codeName;
                });
                let codeIds = params.map((item, index) => {
                    return item.fxAdCodeId;
                });
                this.$Modal.confirm({
                    title: '',
                    content: `` + codeNames.join(","),
                    onOk: () => {

                        this.addcodeData(codeIds.join(","));

                    }
                })
            },
            deleteGroupBox(params) {
                console.log(params);
                let codeGroupNames = params.map((item, index) => {
                    return item.codeName;
                });
                let codeGroupIds = params.map((item, index) => {
                    return item.fxAdCodeGroupId;
                });
                this.$Modal.confirm({
                    title: '',
                    content: `` + codeGroupNames.join(","),
                    onOk: () => {
                        this.deleteCodeGroupData(codeGroupIds.join(","));
                    }
                })
            },
            deleteBox(params) {
                console.log(params);
                let codeNames = params.map((item, index) => {
                    return item.codeName;
                });
                let codeIds = params.map((item, index) => {
                    return item.fxAdCodeId;
                });
                this.$Modal.confirm({
                    title: '',
                    content: `` + codeNames.join(","),
                    onOk: () => {
                        this.deleteCodeData(codeIds.join(","));
                        console.log(codeIds.join(","))
                    }
                })
            },
            stopBox(params) {

                let codeNames = params.map((item, index) => {
                    return item.codeName;
                });
                let codeIds = params.map((item, index) => {
                    return item.fxAdCodeId;
                });
                this.$Modal.confirm({
                    title: '',
                    content: `` + codeNames.join(","),
                    onOk: () => {

                        this.stopCodeData(codeIds.join(","));

                    }
                })
            },

            stopCodeData(codeId) {
                const url = "/code/stopCode.json?codeId=" + codeId;

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();

                    }
                })
            },
            recoverFilterData: function() {
                this.isStop = true;
                this.isAdds = true;
                this.isDeletes = true;
                this.addCodeParams = [];
                this.deleteCodeParams = [];
                this.useCodeParams = [];
                this.getCodeList();
            },


            deleteCodeGroupData(codeGroupId) {
                const url = "/codeGroup/deleteCodeGroup.json?codeGroupId=" + codeGroupId;

                this.$Http.get(url, {}).then(res => {
                    console.log(res)
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.isGroupDalete = true;
                        this.codeGroupList();
                    }
                })
            },

            deleteCodeData(codeId) {
                const url = "/code/deleteCode.json?codeId=" + codeId;
                console.log(codeId)
                this.$Http.get(url, {}).then(res => {
                    console.log(res)
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();
                    }
                })
            },
            addcodeData(codeId) {
                const url = "/code/startCode.json?codeId=" + codeId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.$Message.info(res.body.data);
                        this.recoverFilterData();

                    }
                })
            },

            //################################


            sx() {
                this.showBol2 = !this.showBol2;
                if (this.showBol2) {
                    this.sxIcon = "chevron-up"
                } else {
                    this.sxIcon = "chevron-down"
                }
            },
            sxData() {
                // this.nameLikeGroup
                this.loading = true;
                this.loadingGroup = true;
                this.getCodeList();
                this.codeGroupList();
            },

            // getCodeGroup () {
            //   const url = "/codeGroup/getCodeGroupList.json?nameLike=";
            //   this.$Http.get(url,{}).then(res=>{
            //     console.log(res);
            //     this.codeGroup = res.body.data.result;
            //   })
            // },
            getCodeGroup() {
                const url = "/common/getCodeGroupStageList.json?";
                this.$Http.get(url, {}).then(res => {
                    this.codeGroup = [];
                    if (res.body.status === 0) {
                        console.log(res);
                        let arr = res.body.data.resourceList;
                        let firstObj = {};
                        let secondObj = {};
                        for (let i = 0; i < arr.length; i++) {
                            firstObj = {
                                'value': arr[i].id,
                                'label': arr[i].name,
                            }
                            if (arr[i].childList.length > 0) {
                                firstObj.children = [];
                                for (let j = 0; j < arr[i].childList.length; j++) {
                                    secondObj = {
                                        'value': arr[i].childList[j].id,
                                        'label': arr[i].childList[j].name,
                                    }
                                    firstObj['children'].push(secondObj);
                                }
                            }
                            this.codeGroup.push(firstObj);
                        }

                        console.log(this.codeGroup)
                    }
                })
            },
            changeOption(codeGroupIds) {
                this.loading = true;
                this.loadingGroup = true;
                if (codeGroupIds.length === 2) {
                    this.codeGroupId = codeGroupIds[1]
                } else if (codeGroupIds.length === 1) {
                    this.codeGroupId = codeGroupIds[0]
                } else if (codeGroupIds.length === 0) {
                    this.codeGroupId = "";
                }
                this.codeGroupList();
                this.getCodeList();
            },
            search() {
                // alert(this.value)
                this.loading = true;
                this.pageSize = 10;
                this.pageNo = 1;
                this.data1 = [];
                this.getCodeList();

            },
            searchGroup() {
                this.loadingGroup = true;
                this.pageSizeGroup = 10;
                this.pageNoGroup = 1;
                this.dataGroup = [];
                this.codeGroupList();
            },
            handleCancel() {
                this.nameLike = "";
                this.data1 = [];
                this.getCodeList();
            },
            handleCancel2() {
                this.nameLikeGroup = "";
                this.dataGroup = [];
                this.codeGroupList();
            },
            clickPage(data) {
                console.log(data);
                this.loading = true;
                this.pageNo = data;
                this.data1 = [];
                this.getCodeList();
            },
            changePageSizeCode(data) {
                console.log(data);
                this.pageNo = 1;
                this.pageSize = data;
                this.loading = true;
                this.data1 = [];
                this.getCodeList();
            },
            changePageSizeGroup(data) {
                console.log(data);
                this.pageNoGroup = 1;
                this.pageSizeGroup = data;
                this.loading = true;
                this.data1 = [];
                this.codeGroupList();
            },
            clickPageGroup(data) {
                console.log(data);
                this.loadingGroup = true;
                this.pageNoGroup = data;
                this.data1 = [];
                this.codeGroupList();
            },


            getCodeList() {
                const url = `/code/getCodeList.json?nameLike=${encodeURIComponent(this.nameLike)}&status=${this.status}&isNative=${this.isNative}&useStatus=${this.useStatus}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&codeSize=${this.codeSize}&codeGroupId=${this.codeGroupId}`;

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.loading = false;
                        this.data1 = [];
                        console.log(res);
                        this.loading = false;
                        this.totalCount = res.body.data.totalCount;
                        // this.codeGroup = res.body.data.result;
                        let tablList = '';
                        let codeStatus = "";
                        for (var i = 0; i < res.body.data.result.length; i++) {

                            let arr = res.body.data.result;
                            if (arr[i].codeStatus == 0) {
                                codeStatus = ""
                            } else if (arr[i].codeStatus == 1) {
                                codeStatus = ""
                            } else if (arr[i].codeStatus == 2) {
                                codeStatus = ""
                                    // alert(codeStatus)
                            }
                            tablList = {
                                    'codeName': arr[i].codeName,
                                    'type2': arr[i].statues,
                                    'size': arr[i].width + '*' + arr[i].height,
                                    'type': arr[i].isNative == 0 ? "" : "",
                                    'update': arr[i].updateTime,
                                    'use': arr[i].deliveringCount,
                                    'codeStatusstr': codeStatus,
                                    'codeStatus': arr[i].codeStatus,
                                    'isNative': arr[i].isNative,
                                    "fxAdCodeId": arr[i].fxAdCodeId,
                                    'fxAdCodeGroupId': arr[i].fxAdCodeGroupId,
                                    'deliverIds': arr[i].deliverIds
                                }
                                // console.log(tablList);
                            this.data1.push(tablList);
                        }
                    }
                })

            },
            codeGroupList() {
                const url = `/codeGroup/getCodeGroupList.json?nameLike=${encodeURIComponent(this.nameLikeGroup)}&pageSize=${this.pageSizeGroup}&pageNo=${this.pageNoGroup}&codeGroupId=${this.codeGroupId}`;
                this.$Http.get(url, {}).then(res => {
                    this.dataGroup = [];
                    console.log(res);
                    this.loadingGroup = false;
                    this.totalCountGroup = res.body.data.totalCount;
                    // this.codeGroup = res.body.data.result;
                    let tablList = '';
                    for (var i = 0; i < res.body.data.result.length; i++) {
                        let arr = res.body.data.result;

                        tablList = {
                            'codeName': arr[i].codeGroupName,
                            'type': arr[i].isNative == 0 ? "" : "",
                            'type2': arr[i].codeGroupType == 2 ? "" : "",
                            'update': arr[i].updateTime,
                            'fxAdCodeGroupId': arr[i].fxAdCodeGroupId,
                            'codeGroupStatus': arr[i].codeGroupStatus
                        }
                        this.dataGroup.push(tablList);

                    }
                    console.log(this.dataGroup)
                })
            },
            getTemplateCodeMang() {
                this.$refs.setNewCodeMang.beforeLoad();
                this.$refs.setNewCodeMang.getCodeGroup()
            },
            getTemplateCodeGroup() {
                this.$refs.setNewCodeGroup.beforeLoad();


            },
            filterParentCodeGroup() {
                this.codeGroupList();
                this.getCodeGroup();
                this.$refs.setNewCodeMang.getCodeGroup();

            },
            filterParentCode() {
                this.getCodeList();
                this.getCodeGroup();
            }
    }
}

</script>



