<template>
  <div>
    <Modal
      v-model="modal"
      :title="adUses.useNums+''" :styles="{width:'100%'}" class-name="modal-box">
      <Table class="table" :loading="loading" border :columns="columns" :data="data"></Table>
      <!--  <Page current="10" @on-change='' total=""></Page> -->
      <div slot="footer">
        <Button type="text" @click="exitModal"></Button>
      </div>
    </Modal>
    <ad-native ref="adNative" v-on:filterParent="getFilterData"></ad-native>

    <unad-native ref="unAdNative" v-on:filterParent="getFilterData"></unad-native>

    <ad-native-update ref="adNativeUpdate" v-on:filterParent="getFilterData"></ad-native-update>

    <unad-native-update ref="unAdNativeUpdate" v-on:filterParent="getFilterData"></unad-native-update>

    <use-material ref="useMaterial" v-on:filterParent="getFilterData"></use-material>


  </div>

</template>
<script>
  import adNative from '@/components/pages/deliveryManagement/advertisingManagement/child/adNative'
  import unadNative from '@/components/pages/deliveryManagement/advertisingManagement/child/unAdNative'

  import adNativeUpdate from '@/components/pages/deliveryManagement/advertisingManagement/child/adNativeUpdate'
  import unadNativeUpdate from '@/components/pages/deliveryManagement/advertisingManagement/child/unAdNativeUpdate'

  import useMaterial from '@/components/pages/deliveryManagement/materialManagement/child/useMaterial'


  export default {
    components: {adNative, unadNative, adNativeUpdate, unadNativeUpdate, useMaterial},
    data() {
      return {
        modal: false,
        loading: false,
        deliverId: '',
        columns: [

          {
            title: '',
            key: 'name',

          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'state'
          },
          {
            title: '',
            key: 'codeBit',
            render: (h, params) => {

              let deliverCodeList = params.row.deliverCodeList;
              if (deliverCodeList.length == 1) {
                let codeNames = deliverCodeList[0].codeName;
                return h('div', codeNames);

              }
              if (deliverCodeList.length > 1) {
                let codeNum = deliverCodeList.length;
                let codeNames = "";
                deliverCodeList.forEach((item) => {
                  codeNames += item.codeName + "\n"

                });
                return h('div', [h('Poptip', {
                    props: {
                      content: codeNames
                    }

                  }, [h('a', codeNum + "")]
                )])

              }

            }
          },
          {
            title: '',
            key: 'material',
            render: (h, params) => {
              let deliverMaterialList = params.row.deliverMaterialList;

              let materialIds = deliverMaterialList.map((item, index) => {
                return item.id;
              });


              return h('div', [
                h('a', {
                  style: {
                    "text-decoration": 'underline'
                  },
                  on: {
                    click: () => {
                      this.$refs.useMaterial.beforeLoad(deliverMaterialList.length);
                      this.$refs.useMaterial.getMaterialListByIds(materialIds.join(","));
                    }
                  }
                }, deliverMaterialList.length + "")
              ])

              // if(deliverMaterialList.length==1){
              //     let materialNames=deliverMaterialList[0].materialName;
              //     return h('div',materialNames);
              //
              // }
              // if(deliverMaterialList.length>1){
              //     let materialNum=deliverMaterialList.length;
              //     let materialNames="";
              //     deliverMaterialList.forEach((item)=>{
              //         materialNames+=item.materialName+"\n"
              //
              //     });
              //     return h('div',[h('Poptip',{
              //          props: {
              //             content:materialNames
              //          }
              //
              //     },[h('a',materialNum+"")]
              //
              //     )])
              //
              // }
            }
          },
          {
            title: '',
            key: 'totalWeightNum',
            width: 80
          },
          {
            title: '',
            key: 'priority',
            width: 80
          },
          {
            title: '',
            key: 'date',
            className: 'deliver-date',
            width: 200,
            render: (h, params) => {

              if (params.row.dateType == 1) {

                return h('a',

                  {
                    on: {
                      click: () => {

                        const deliverTimeList = params.row.deliverTimeList.reverse();


                        let timeDesc = '';

                        for (let i = 0; i < deliverTimeList.length; i++) {

                          let startTime = deliverTimeList[i].startTime.substring(0, 10);
                          let endTime = deliverTimeList[i].endTime.substring(0, 10);

                          if (startTime == endTime) {

                            timeDesc += startTime + "<br/>"
                          } else {
                            timeDesc += startTime + "" + endTime + "<br/>"
                          }


                        }

                        this.$Modal.info({
                          title: "",
                          content: timeDesc
                        });

                      }
                    }
                  },

                  [
                    h('Icon', {
                      props: {
                        type: 'plus-circled'
                      }
                    }),
                    h('span', params.row.date)
                  ]);


              } else {
                return h('span', params.row.date);
              }


            }


          },
          {
            title: '',
            key: 'schedule',
            width: 150,
            render: (h, params) => {
              return h('div') , [
                h('Progress', {
                  props: {
                    percent: params.row.schedule,
                    status: 'active',
                    strokewidth: 50

                  },
                })

              ]
            }
          },
          {
            title: '',
            key: 'operate',
            width: 150,
            render: (h, params) => {
              let stateNums = params.row.stateNums;
              let typeNum = params.row.typeNum;
              let deliverId = params.row.deliverId;
              if (stateNums == 0) {//  
                return h('div', [h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      if (typeNum == 0) {//
                        this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                      }
                      if (typeNum == 1) {//
                        this.$refs.adNativeUpdate.beforeLoad(deliverId);

                      }

                    }
                  }
                }, '')]);

              }
              if (stateNums == 1) {//   
                return h('div',
                  [h('Button', {
                    props: {
                      type: 'info',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px',
                      backgroundColor: '#1fa2fd',
                      borderColor: '#1fa2fd'
                    },
                    on: {
                      click: () => {
                        if (typeNum == 0) {//
                          this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                        }
                        if (typeNum == 1) {//
                          this.$refs.adNativeUpdate.beforeLoad(deliverId);

                        }

                      }
                    }
                  }, ''),
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small',
                        // disabled:this.disabled2
                      },
                      style: {

                        backgroundColor: '#ea0b00',
                        borderColor: '#ea0b00'
                      },
                      on: {
                        click: () => {
                          let v = [];
                          v.push(params.row);

                          this.deleteBox(v);


                        }
                      }
                    }, '')]);

              }
              if (stateNums == 2) {//   
                return h('div', [h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      if (typeNum == 0) {//
                        this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                      }
                      if (typeNum == 1) {//
                        this.$refs.adNativeUpdate.beforeLoad(deliverId);

                      }

                    }
                  }
                }, ''), h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                    // disabled:this.disabled2
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);

                    }
                  }
                }, '')]);


              }
              if (stateNums == 3) {//   
                return h('div', [h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      if (typeNum == 0) {//
                        this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                      }
                      if (typeNum == 1) {//
                        this.$refs.adNativeUpdate.beforeLoad(deliverId);

                      }

                    }
                  }
                }, ''), h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                    // disabled:this.disabled2
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);


                    }
                  }
                }, '')]);


              }
              if (stateNums == 4) {//   
                return h('div', [h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      if (typeNum == 0) {//
                        this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                      }
                      if (typeNum == 1) {//
                        this.$refs.adNativeUpdate.beforeLoad(deliverId);

                      }

                    }
                  }
                }, ''), h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                    // disabled:this.disabled2
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {


                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);
                    }
                  }
                }, '')]);


              }
              if (stateNums == 5) {//   
                return h('div', [h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      if (typeNum == 0) {//
                        this.$refs.unAdNativeUpdate.beforeLoad(deliverId);

                      }
                      if (typeNum == 1) {//
                        this.$refs.adNativeUpdate.beforeLoad(deliverId);

                      }

                    }
                  }
                }, ''), h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                    // disabled:this.disabled2
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);

                    }
                  }
                }, '')]);


              }

            }


          }

        ],

        data: []


      }
    },
    created() {

    },
    mounted() {

    },
    props: ['adUses'],
    methods: {
      beforeLoad() {
        this.modal = true;

      },

      exitModal() {
        this.modal = false;
        this.$emit("filterParent");


      },

      //******************************
      getDeliverById(deliverId) {
        this.deliverId = deliverId;
        this.loading = true;
        const url = `/deliver/getDeliverByIds.json?deliverIds=${deliverId}`;
        this.$Http.get(url, {}).then(res => {
          this.loading = false;
          if (res.body.status == 0) {
            this.data = [];
            console.log(res.body.data);
            let result = res.body.data.list;

            for (let item of result) {
              let isNative = "";
              let deliverStatus = "";
              let level = "";


              if (item.isNative == 0) {
                isNative = ""
              } else if (item.isNative == 1) {
                isNative = ""
              }

              if (item.deliverStatus == 0) {
                deliverStatus = "";

                // this.disabled2 = 'false';
              } else if (item.deliverStatus == 1) {
                deliverStatus = ""
              } else if (item.deliverStatus == 2) {
                deliverStatus = ""
              } else if (item.deliverStatus == 3) {
                deliverStatus = ""
              } else if (item.deliverStatus == 4) {
                deliverStatus = ""
              } else if (item.deliverStatus == 5) {
                deliverStatus = ""
              }
              if (item.level == 1) {
                level = ""
              } else if (item.level == 2) {
                level = ""
              } else if (item.level == 3) {
                level = ""
              } else if (item.level == 4) {
                level = ""
              }
              var startDate = item.startDate;
              var endDate = item.endDate;
              let percent = item.progressRate;

              let subDate = startDate + "-" + endDate;

              // console.log(arr[key].fxadDeliverCodeMaterialList);
              let resultList = {
                'name': item.deliverName,
                'type': isNative,
                'typeNum': item.isNative,
                'state': deliverStatus,
                'stateNums': item.deliverStatus,
                'totalWeightNum': item.totalWeightNum,
                'deliverCodeList': item.fxadDeliverCodeList,
                'deliverMaterialList': item.fxadDeliverMaterialList,
                'priority': level,
                'date': subDate,
                'dateType': item.dateType,
                'schedule': percent,
                'deliverId': item.fxAdDeliverId,
                'deliverTimeList': item.deliverTimeList
              }


              this.data.push(resultList);


            }


          }


        })
      },
      getFilterData() {
        this.pageSize = 10;
        this.pageNo = 1;
        this.recoverFilterData();
      },
      recoverFilterData: function () {
        this.isStops = true;
        this.isAdds = true;
        this.isDeletes = true;
        this.stopAdDeliverParams = [];
        this.addAdDeliverParams = [];
        this.deleteAdDeliverParams = [];
        this.getDeliverById(this.deliverId);
      },
      deleteBox(params) {

        let deliverNames = params.map((item, index) => {
          return item.name;
        });
        let deliverIds = params.map((item, index) => {
          return item.deliverId;
        });
        this.$Modal.confirm({
          title: '',
          content: `` + deliverNames.join(","),
          onOk: () => {

            this.deleteDeliverData(deliverIds.join(","));

          }
        })
      },
      deleteDeliverData(AdDeliverId) {

        const url = "/deliver/deleteDeliver.json?deliverId=" + AdDeliverId;

        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            this.$Message.info("");
            this.data.splice(this.data.findIndex(item => item.deliverId == AdDeliverId), 1)

            this.adUses.useNums = this.data.length;

          }
        })
      },
    }
  }
</script>
<style>
</style>



