<template>
  <div>

    <Modal
      v-model="modal"
      title="" :styles="{width:'100%'}" class-name="modal-box">
      <div>
        <div style="display:flex;">
          <div></div>
          <RadioGroup @on-change="selectRadio" v-model="radios">
            <Radio :value="1" :label="1">
              <span></span>
            </Radio>
            <Radio :value="2" :label="2">
              <span></span>
            </Radio>
          </RadioGroup>

        </div>
        <div>
          <div class="site" v-show="site">
            <ul>
              <li class="sote-child">
                <span class="label-left"></span>
                <Select @on-change="" v-model="isNative" style="width:200px">
                  <Option :value="1"></Option>
                  <Option :value="0"></Option>
                </Select>
              </li>
            </ul>
          </div>
          <div class="channel" v-show="channel">
            <ul>
              <li class="sote-child">
                <span class="label-left"></span>
                <Select @on-change="" v-model="parentGroupId" style="width:200px">
                  <Option v-for="(item,index) in websiteList" :key="index" :value="item.id">{{item.name}}</Option>
                </Select>
              </li>
            </ul>
          </div>
          <div class="sote-child">
            <span class="label-left"></span>
            <Input v-model="groupName" placeholder="" style="width: 300px"></Input>
            <span :class="{'label-right-active':isUserAtive}" class="label-right">{{soltUserName}}</span>
          </div>
        </div>
      </div>

      <div slot="footer">
        <Button type="text" @click="modal=false"></Button>
        <Button class="btnColor" type="primary" @click="updataCodeGroup"></Button>
      </div>
    </Modal>
  </div>
</template>

<style scoped>
  * {
    list-style: none;
  }

  .sote-child {
    margin-top: 20px;
  }

  .btn-wraps {
    margin-top: 100px;
  }

</style>
<script>
  // import topTitle from '@/components/common/topTitle'
  export default {
    // components:{topTitle},
    data() {
      return {
        modal: false,
        radios: 1,
        isNative: '',
        site: true,
        channel: false,
        soltUserName: '100',
        types: '',
        groupName: '',
        isUserAtive: false,
        websiteList: [],
        parentGroupId: 0
      }
    },
    created() {

      this.getWebsite();
    },
    methods: {
      beforeLoad() {
        this.radios = "1"
        this.isNative = "";
        this.groupName = "";
        this.parentGroupId = "";
        this.modal = true;
      },
      selectRadio(data) {

        if (data == "1") {
          this.site = true;
          this.channel = false;
        } else {
          this.site = false;
          this.channel = true;
        }


      },
      getWebsite() {
        const url = `/common/getWebsiteList.json?`;
        this.$Http.get(url, {}).then(res => {

          console.log(res);
          if (res.body.status === 0) {
            this.websiteList = res.body.data.websiteList;
          }
        });
      },
      updataCodeGroup() {
        var url = `/codeGroup/updateCodeGroup.json?codeGroupType=${this.radios}&isNative=${this.isNative}&codeGroupName=${encodeURIComponent(this.groupName)}&parentGroupId=${this.parentGroupId}&codeGroupId=${this.codeGroupId}`;
        this.$Http.get(url, {}).then(res => {

          console.log(res);

          if (res.body.status === 0) {
            this.modal = false;
            this.$emit("filterParent");
          }
        });
      },
      getCodeGroupById(codeGroupId) {
        this.codeGroupId = codeGroupId;
        const url = `/codeGroup/getCodeGroupById.json?codeGroupId=${codeGroupId}`;
        this.$Http.get(url, {}).then(res => {

          console.log(res);

          if (res.body.status === 0) {
            this.groupName = res.body.data.codeGroupName;
            this.radios = res.body.data.codeGroupType;
            this.isNative = res.body.data.isNative;
            this.parentGroupId = res.body.data.parentGroupId;
            console.log(this.parentGroupId);
            if (this.radios == "1") {
              this.site = true;
              this.channel = false;
            } else {
              this.site = false;
              this.channel = true;
            }

          }
        });
      },

    }

  }
</script>



