<template>
  <div>

    <Modal
      v-model="modal"
      title="" :styles="{width:'100%'}" class-name="modal-box">
      <div>
        <div style="display:flex;">
          <div></div>
          <RadioGroup @on-change="selectRadio" v-model="radios">
            <Radio value="1" label="1">
              <span></span>
            </Radio>
            <Radio value="2" label="2">
              <span></span>
            </Radio>
          </RadioGroup>

        </div>
        <div>
          <div class="site" v-show="site">
            <ul>
              <li class="sote-child">
                <span class="label-left"></span>
                <Select @on-change="" v-model="isNative" style="width:200px">
                  <Option value="1"></Option>
                  <Option value="0"></Option>
                </Select>
              </li>
            </ul>
          </div>
          <div class="channel" v-show="channel">
            <ul>
              <li class="sote-child">
                <span class="label-left"></span>
                <Select @on-change="" v-model="parentGroupId" style="width:200px">
                  <Option v-for="(item,index) in websiteList" :key="index" :value="item.id">{{item.name}}</Option>
                </Select>
              </li>
            </ul>
          </div>
          <div class="sote-child">
            <span class="label-left"></span>
            <Input v-model="groupName" @on-blur='userDataNum' @on-focus='onFocusName' placeholder=""
                   style="width: 300px"></Input>
            <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
          </div>
        </div>
      </div>

      <div slot="footer">
        <Button type="text" @click="cancel"></Button>
        <Button class="btnColor" type="primary" @click="createCodeGroup"></Button>
      </div>
    </Modal>
  </div>
</template>

<style>
  * {
    list-style: none;
  }

  .sote-child {
    margin-top: 20px;
  }

  .btn-wraps {
    margin-top: 100px;
  }

</style>
<script>
  // import topTitle from '@/components/common/topTitle'
  export default {
    // components:{topTitle},
    data() {
      return {
        isAtive: false,
        modal: false,
        radios: '1',
        isNative: '',
        site: true,
        channel: false,
        soltNameLen: '100',
        types: '',
        groupName: '',
        isUserAtive: false,
        websiteList: [],
        parentGroupId: ''
      }
    },
    created() {

      this.getWebsite();
    },
    methods: {
      beforeLoad() {
        this.soltNameLen = "100"
        this.isAtive = false;
        this.radios = "1"
        this.isNative = "";
        this.groupName = "";
        this.parentGroupId = "";
        if (this.radios == "1") {
          this.site = true;
          this.channel = false;
        } else {
          this.site = false;
          this.channel = true;
        }

        this.modal = true;
      },
      cancel() {
        this.soltNameLen = "100"
        this.isAtive = false;
        this.modal = false;
      },
      selectRadio(data) {
        if (data == "1") {
          this.site = true;
          this.channel = false;
        } else {
          this.site = false;
          this.channel = true;
        }


      },
      getWebsite() {
        const url = `/common/getWebsiteList.json?`;
        this.$Http.get(url, {}).then(res => {

          console.log(res);
          if (res.body.status === 0) {
            this.websiteList = res.body.data.websiteList;
          }
        });
      },
      createCodeGroup() {
        var url = `/codeGroup/createCodeGroup.json?codeGroupType=${this.radios}&codeGroupName=${encodeURIComponent(this.groupName)}`;
        if (this.site) {
          url += `&isNative=${this.isNative}`
        } else {
          url += `&parentGroupId=${this.parentGroupId}`
        }

        this.$Http.get(url, {}).then(res => {

          console.log(res);

          if (res.body.status === 0) {
            this.modal = false;
            this.getWebsite();
            this.$emit("filterParent");
          }
        });
      },
      onFocusName() {
        this.soltNameLen = "100";
        this.isAtive = false;

      },
      userDataNum() {
        //0-100
        if (this.groupName) {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.groupName.match(reg);
          if (r == null) {
            this.soltNameLen = "100"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = ""
          this.isAtive = true;
        }
      },

    }

  }
</script>



