<style>

* {
    list-style: none;
    padding: 0;
    margin: 0;
}

.newCode-child {
    margin-top: 20px;
}

.btn-wraps {
    margin-top: 100px;
}

.label-right-active {
    color: red !important;
}

</style>

<template>

<div class="">
    <Modal v-model="modal" title="" :styles="{width:'100%'}" class-name="modal-box">
        <ul class="newCode-wrap">
            <li class="newCode-child">
                <span></span>
                <!-- <Cascader :data="codeGroup" v-model="codeCascaderModel" @on-change="changeOption" style="width:200px;display:inline-block;" change-on-select filterable>
                </Cascader> -->
                <div class="main-element">
                    <el-cascader style="width:200px" :options="codeGroup" @change="changeOption" v-model="codeCascaderModel" change-on-select filterable clearable></el-cascader>
                </div>
            </li>
            <li class="newCode-child">
                <span></span>
                <Input v-model="codeName" placeholder="" style="width: 300px" @on-blur='checkCodeName' @on-focus='focusName'></Input>
                <span :class="{'label-right-active':invalidName}" class="label-right">{{invalidNameTip}}</span>
            </li>
            <li class="newCode-child">
                <span></span>
                <Input v-model="codeDesc" type="textarea" :rows="8" placeholder="" style="width: 300px" @on-blur='checkCodeDesc' @on-focus='focusDesc'></Input>
                <span :class="{'label-right-active':invalidDesc}" class="label-right">{{invalidDescTip}}</span>
            </li>
        </ul>
        <div slot="footer">
            <Button type="text" @click="modal=false"></Button>
            <Button class="btnColor" type="primary" @click="updataCode" v-text="saveDesc"></Button>
        </div>
    </Modal>
</div>

</template>

<script>

export default {
    data() {
            return {
              selectedOptions:[],
                modal: false,
                codeMang: '',
                codeName: '',
                codeGroup: [],
                fxAdCodeGroupId: "",
                codeDesc: '',
                invalidName: false,
                invalidDesc: false,
                invalidNameTip: '30',
                invalidDescTip: '100',
                isNative: "",
                saveDesc: '',
                codeCascaderModel: [1, 2],
            }
        },
        created() {
            this.getCodeGroup();
        },
        methods: {
            beforeLoad(status) {

                    console.log(status + "       ===")

                    this.codeName = ""
                    this.modal = true

                    if (status == 0) {
                        this.saveDesc = '';
                    } else {
                        this.saveDesc = '';

                    }


                },
                checkCodeName() {
                    if (this.codeName) {
                        var reg = /^[\s\S]{1,30}$/;
                        var r = this.codeName.match(reg);
                        if (r == null) {
                            this.invalidNameTip = "30";
                            this.invalidName = true;
                        }
                    } else {
                        this.invalidNameTip = "";
                        this.invalidName = true;
                    }
                },
                focusName() {
                    this.invalidNameTip = "30";
                    this.invalidName = false;
                },
                checkCodeDesc() {
                    if (this.codeDesc) {
                        var reg = /^[\s\S]{0,100}$/;
                        var r = this.codeDesc.match(reg);
                        if (r == null) {
                            this.invalidDescTip = "100";
                            this.invalidDesc = true;
                        }
                    }
                },
                focusDesc() {
                    this.invalidDescTip = "100";
                    this.invalidDesc = false;
                },
                jsonToExtend(key, exkey) {
                    for (var i = 0; i < json.length; i++) {
                        json[i][key] = json[i][exkey]; //'text'
                        delete json[i][exkey]; //key'text'
                    }
                },
                getCodeGroup() {
                    const url = "/common/getCodeGroupStageList.json?";
                    let cascaderObj = {};
                    this.$Http.get(url, {}).then(res => {
                        this.codeGroup = [];
                        if (res.body.status === 0) {
                            console.log(res);
                            let arr = res.body.data.resourceList;
                            let firstObj = {};
                            let secondObj = {};
                            for (let i = 0; i < arr.length; i++) {
                                firstObj = {
                                    'value': arr[i].id,
                                    'label': arr[i].name,
                                }
                                if (arr[i].childList.length > 0) {
                                    firstObj.children = [];
                                    for (let j = 0; j < arr[i].childList.length; j++) {
                                        secondObj = {
                                            'value': arr[i].childList[j].id,
                                            'label': arr[i].childList[j].name,
                                        }
                                        firstObj['children'].push(secondObj);
                                    }
                                }
                                this.codeGroup.push(firstObj);
                            }
                            console.log(this.codeGroup)
                        }
                    })
                },
                changeOption(codeGroupIds, selectedData) {

                    if (codeGroupIds.length === 2) {
                        this.codeGroupId = codeGroupIds[1]
                    } else {
                        this.codeGroupId = codeGroupIds[0]
                    }
                    // this.codeGroupId = codeGroupId;
                    //this.codeGroupList();
                    console.log("///////////////////////")
                    console.log(codeGroupIds, selectedData, this.codeCascaderModel);
                    console.log("///////////////////////")
                        //this.getCodeGroup();
                },
                upCodeAjax() {
                    const url = `code/updateCode.json?codeGroupId=${this.codeGroupId}&codeName=${encodeURIComponent(this.codeName)}&isNative=${this.isNative}&codeId=${this.codeId}&codeDesc=${encodeURIComponent(this.codeDesc)}`;
                    this.$Http.get(url, {}).then(res => {
                        if (res.body.status == 0) {
                            console.log(res);
                            this.modal = false;
                            this.$emit("filterParent");
                        }

                    })
                },
                updataCode() {
                    // if (this.invalidName || this.invalidDesc) {
                    //     return false;
                    // }
                    if (this.codeName && this.codeCascaderModel.length) {
                        this.upCodeAjax();
                    } else {
                        if (!this.codeCascaderModel.length) {
                            this.$Message.error('!');
                            return false;
                        }
                        if (!this.codeName) {
                            this.$Message.error('!');
                            return false;
                        }
                    }
                },
                getCodeById(codeId) {
                    this.codeCascaderModel = [];
                    const url = `/code/getCodeById.json?codeId=${codeId}`;
                    this.$Http.get(url, {}).then(res => {
                        if (res.body.status == 0) {
                            let body = res.body.data;
                            console.log(res);
                            this.codeName = body.codeName;
                            this.codeGroupId = body.fxAdCodeGroupId;
                            this.codeId = body.fxAdCodeId;
                            this.codeDesc = body.codeDesc;
                            let parentGroupId = parseInt(body.parentGroupId ? body.parentGroupId : 0);
                            let fxAdCodeGroupId = parseInt(body.fxAdCodeGroupId);
                            let fxAdcodeGroupIdsArr = [parentGroupId, fxAdCodeGroupId]
                            this.codeCascaderModel = fxAdcodeGroupIdsArr
                            console.log(this.codeCascaderModel)
                            this.$emit("filterParent");
                        }

                    })
                }
        }
}

</script>



