<style>

* {
    list-style: none;
    padding: 0;
    margin: 0;
}

.newCode-child {
    margin-top: 20px;
}

.btn-wraps {
    margin-top: 100px;
}

.label-right-active {
    color: red !important;
}

</style>

<template>

<div class="">
    <Modal v-model="modal" title="" :styles="{width:'100%'}" class-name="modal-box">
        <ul class="newCode-wrap">
            <li class="newCode-child">
                <span></span>
                <!-- <Select @on-change="" v-model="fxAdCodeGroupId" style="width:200px">
                           <Option v-for="item in codeGroup" :value="item.fxAdCodeGroupId">{{item.codeGroupName}}</Option>

                    </Select> -->
                <!-- <Cascader v-model="fxAdCodeGroupId" :data="codeGroup" @on-change="changeOption" style="width:200px;display:inline-block;" change-on-select filterable>

                </Cascader> -->
                <!-- <Input v-model="codeMang" placeholder="" style="width: 300px"></Input> -->
                <div class="main-element">
                    <el-cascader style="width:200px" :options="codeGroup" @change="changeOption" v-model="fxAdCodeGroupId" change-on-select filterable clearable></el-cascader>
                </div>
            </li>
            <li class="newCode-child">
                <span></span>
                <Input v-model="codeName" placeholder="" style="width: 300px" @on-blur='checkCodeName' @on-focus='focusName'></Input>
                <span :class="{'label-right-active':invalidName}" class="label-right">{{invalidNameTip}}</span>
            </li>
            <li class="newCode-child">
                <span></span>
                <Input v-model="codeDesc" type="textarea" :rows="8" placeholder="" style="width: 300px" @on-blur='checkCodeDesc' @on-focus='focusDesc'></Input>
                <span :class="{'label-right-active':invalidDesc}" class="label-right">{{invalidDescTip}}</span>
            </li>
        </ul>
        <div slot="footer">
            <Button type="text" @click="modal=false"></Button>
            <Button class="btnColor" type="primary" @click="creatCode"></Button>
        </div>
    </Modal>
</div>

</template>

<script>

export default {
    data() {
            return {
                selectedOptions: [],
                modal: false,
                codeMang: '',
                codeName: '',
                codeGroup: [],
                fxAdCodeGroupId: [],
                codeDesc: '',
                invalidName: false,
                invalidDesc: false,
                invalidNameTip: '30',
                invalidDescTip: '100',
                isNative: 1
            }
        },
        created() {

            this.getCodeGroup();
        },
        methods: {
            beforeLoad() {
                    this.fxAdCodeGroupId = [];
                    this.codeName = "";
                    this.codeDesc = "";
                    this.modal = true
                },
                checkCodeName() {
                    if (this.codeName) {
                        var reg = /[^\%\&\$]{1,30}$/;
                        var r = this.codeName.match(reg);
                        if (r == null) {
                            this.invalidNameTip = "30";
                            this.invalidName = true;
                        }
                    } else {
                        this.invalidNameTip = "";
                        this.invalidName = true;
                    }
                },
                focusName() {
                    this.invalidNameTip = "30";
                    this.invalidName = false;
                },
                checkCodeDesc() {
                    if (this.codeDesc) {
                        var reg = /^[\s\S]{0,100}$/;
                        var r = this.codeDesc.match(reg);
                        if (r == null) {
                            this.invalidDescTip = "100";
                            this.invalidDesc = true;
                        }
                    }
                },
                focusDesc() {
                    this.invalidDescTip = "100";
                    this.invalidDesc = false;
                },
                getCodeGroup() {
                    const url = "/common/getCodeGroupStageList.json?";
                    let cascaderObj = {};
                    this.$Http.get(url, {}).then(res => {
                        this.codeGroup = [];
                        if (res.body.status === 0) {
                            console.log(res);
                            let arr = res.body.data.resourceList;
                            let firstObj = {};
                            let secondObj = {};
                            for (let i = 0; i < arr.length; i++) {
                                firstObj = {
                                    'value': arr[i].id,
                                    'label': arr[i].name
                                }
                                if (arr[i].childList.length > 0) {
                                    firstObj.children = [];
                                    for (let j = 0; j < arr[i].childList.length; j++) {
                                        secondObj = {
                                            'value': arr[i].childList[j].id,
                                            'label': arr[i].childList[j].name,
                                        }
                                        firstObj['children'].push(secondObj);
                                    }
                                }

                                this.codeGroup.push(firstObj);
                            }

                            console.log(this.codeGroup)
                        }
                    })
                },
                changeOption(codeGroupIds) {


                    if (codeGroupIds.length === 2) {
                        this.codeGroupId = codeGroupIds[1]
                    } else {
                        this.codeGroupId = codeGroupIds[0]
                    }
                    console.log(this.codeGroupId)
                        // this.codeGroupId = codeGroupId;
                        // this.getCodeGroup();
                        // this.codeGroupList();
                },
                codeAjax() {
                    const url = `/code/createCode.json?codeGroupId=${this.codeGroupId}&codeName=${encodeURIComponent(this.codeName)}&isNative=${this.isNative}&codeDesc=${encodeURIComponent(this.codeDesc)}`;
                    this.$Http.get(url, {}).then(res => {
                        if (res.body.status == 0) {
                            console.log(res);
                            this.modal = false;
                            this.$emit("filterParent");
                        }
                    })
                },
                creatCode() {
                    if (this.codeName && this.fxAdCodeGroupId.length) {
                        this.codeAjax();
                    } else {
                        if (!this.fxAdCodeGroupId.length) {
                            this.$Message.error('!');
                            return false;
                        }
                        if (!this.codeName) {
                            this.$Message.error('!');
                            return false;
                        }
                    }
                }
        }
}

</script>



