<style>

  * {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .newCode-child {
    margin-top: 20px;
  }

  .btn-wraps {
    margin-top: 100px;
  }

  .flex-box-content {
    display: -webkit-box;
    -webkit-box-align: center;
    display: flex;
    align-items: center;
  }

  .flex-box-content span {
    display: block;
    margin-right: 5px;
  }

  .flex-box-content .text {
    position: relative;
    display: inline-block;
    line-height: 1.5;
    padding: 4px 7px;
    border-radius: 4px;
    height: auto;
    min-height: 32px;
    font-size: 14px;
    border: 1px solid #dddee1;
    outline: none;
    vertical-align: bottom;
    color: #495060;
    background-color: #fff;
    cursor: text;
    transition: all 0.2s;
  }

  .flex-box-content .text:focus {
    border-color: rgba(45, 140, 240, 0.6);
  }

</style>

<template>

  <div class="">
    <Modal v-model="modal" title="" :styles="{width:'100%'}" class-name="modal-box">
      <ul class="newCode-wrap">

        <li class="newCode-child flex-box-content">
          <span v-text="'<head>'"></span>
          <textarea class="text" v-model="headDesc" type="textarea" rows="1" placeholder=""
                    style="width: 800px"></textarea>
        </li>
        <li class="newCode-child flex-box-content">
          <span v-text="'<body>'"></span>
          <textarea class="text" v-model="bodyDesc" type="textarea" rows="14" placeholder=""
                    style="width: 800px"></textarea>
        </li>
      </ul>
      <div slot="footer">
        <Button type="text" @click="modal=false"></Button>
        <Button class="btnColor" type="primary" @click="modal=false"></Button>
      </div>
    </Modal>
  </div>

</template>

<script>

  export default {
    data() {
      return {
        modal: false,
        codeMang: '',
        codeName: '',
        codeGroup: [],
        fxAdCodeId: "",
        codeDesc: '',
        isNative: "",
        saveDesc: '',
        headDesc: '',
        bodyDesc: '',
        codeCascaderModel: [1, 2],
      }
    },
    created() {
      //this.getCodeGroup();
    },
    methods: {
      beforeLoad(fxAdCodeId) {
        this.modal = true;

        let headDesc = '<@ src="http://192.168.30.190:8091/static/js/ad.js"></@>';

        this.headDesc = headDesc.replace(/@/g, "script");


        let bodyDesc = '<@>\n' +
          '\tvar s = "_" + Math.random().toString(36).slice(2);\n' +
          '\tdocument.write(\'<div id="\' + s + \'"></div>\');\n' +
          '\t// jQuery $(document).ready \n' +
          '\tShowAd({\n' +
          '\t\tcodeId: #,\n' +
          '\t\tcontainer: s\n' +
          '\t})\n' +
          '\t// iframe \n' +
          '\tShowAdAsync({\n' +
          '\t\tcodeId: #,\n' +
          '\t\tcontainer: s\n' +
          '\t})\n' +
          '</@>';

        this.bodyDesc = bodyDesc.replace(/@/g, "script").replace(/#/g, fxAdCodeId);

      }

    }
  }

</script>



